import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import DogHistory from '../../components/dog/history';

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
		await this.loadFeed();
    }

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed();
    }


	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }

	render() {
		const { userStore } = this.props.stores;
		const { workouts, historyFeed, workoutSummary } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 mb-4'>
						{/* <h5 class='mt-4'>user</h5>
						{JSON.stringify(user)} */}
						{/* <h5 class='mt-4'>team</h5>
						{JSON.stringify(team)} */}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<h5>Siste fra hundene</h5>
						{historyFeed && historyFeed.map(line => (
							<DogHistory stores={this.props.stores} line={line} />
						))}
					</div>
				</div>
				{/* <div class='row'>
					<div class='col-12 mb-4'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<Dog stores={this.props.stores} dog={dog} />
							))}
						</div>
					</div>
				</div> */}
				<div class='row'>
					<div class='col-12 mb-4'>				
						<h5>Siste treningsturer - {workouts.length} stk</h5>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>	
						<h5 class='mt-4'>Treningsoversikt</h5>
						{/* {JSON.stringify(workoutSummary)} */}
						{workoutSummary && workoutSummary.map(summary => (
							<div class='col-12 col-sm-12 col-lg-2 mt-2 p-2 d-flex'>
								{/* {JSON.stringify(workout, null, 4)} */}
								<div class={`text-center border rounded-circle imageRounded`}>
									<span class={`text-muted mt-4`} style='font-size: 40px;'>{summary._id.week}</span>
								</div>
								<div class='flex-grow-1 pl-3'>
									<h5 class='my-0'>{summary._id.week} - {summary._id.year}</h5>
									<small class='text-muted'>
										<i class='fas fa-wave-square' /> {summary.count}
										<i class='fas fa-road ml-2' /> {summary.distanceKm}km
										<i class='fas fa-mountain ml-2' /> {summary.elevation}m
										<i class='fas fa-stopwatch ml-2' /> {util.secToHms(summary.duration)}
										<i class='fas fa-tachometer-alt ml-2' /> {summary.speedAvg}km/t
									</small>
								</div>
							</div>
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
